import React, { useEffect, useState } from 'react';
import Head from 'next/head';
import '../styles/teams.css';  // Assuming you have this CSS file for styling

const TeamsTable = () => {
  const [teams, setTeams] = useState([]);
  const [currentUser, setCurrentUser] = useState(null);

  useEffect(() => {
    fetchTeams();
    checkLoginStatus();
  }, []);

  const fetchTeams = () => {
    fetch('http://localhost:3000/teams')  // Replace with your server endpoint if different
      .then(response => response.json())
      .then(data => {
        setTeams(data);
      })
      .catch(error => {
        console.error('Error fetching teams:', error);
      });
  };

  const deleteTeam = async (teamId) => {
    if (!teamId) {
      alert('Invalid team ID');
      return;
    }

    const confirmation = confirm("Are you sure you want to delete this team? This action cannot be undone.");

    if (confirmation) {
      try {
        const response = await fetch(`http://localhost:3000/teams/${teamId}`, {
          method: 'DELETE',
        });

        if (response.ok) {
          alert('Team deleted successfully!');
          fetchTeams(); // Refresh the teams list
        } else {
          const errorData = await response.json();
          alert(errorData.error);
        }
      } catch (err) {
        alert('Failed to delete team');
        console.error(err);
      }
    }
  };

  const checkLoginStatus = () => {
    const token = localStorage.getItem('token');
    if (!token) {
      console.error('No token found');
      return;
    }

    fetch('http://localhost:3000/check-login', {
      method: 'GET',
      headers: {
        'Authorization': `Bearer ${token}`
      }
    })
      .then(response => {
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        return response.json();
      })
      .then(data => {
        if (data.loggedIn) {
          setCurrentUser(data.username);
        } else {
          localStorage.removeItem('token');
        }
      })
      .catch(error => {
        console.error('Error checking login status:', error);
        localStorage.removeItem('token');
      });
  };

  const logout = () => {
    fetch('http://localhost:3000/logout', {  // Ensure this matches your logout endpoint
      method: 'POST',
    })
      .then(response => response.json())
      .then(data => {
        if (data.message === 'Logout successful') {
          localStorage.removeItem('token');
          setCurrentUser(null);
          alert('Logged out successfully.');
          window.location.reload();  // Reload the page to reflect the logged-out status
        } else {
          alert('Error during logout.');
        }
      })
      .catch(error => {
        console.error('Error during logout:', error);
        alert('Error during logout: ' + error.message);
      });
  };

  return (
    <>
      <Head>
        <meta charSet="UTF-8" />
        <meta name="viewport" content="width=device-width, initial-scale=1.0" />
        <title>Teams Table</title>
        <link href="https://cdn.jsdelivr.net/npm/bootstrap@5.3.0-alpha3/dist/css/bootstrap.min.css" rel="stylesheet" />
      </Head>
      <div className="bg-light">
        <nav className="navbar navbar-expand-lg navbar-light fixed-top mask-custom shadow-0">
          <div className="container">
            <a className="navbar-brand" href="index.html">
              <img src="/oly.png" alt="Olympiacos Logo" style={{ height: '60px', width: '60px', objectFit: 'contain', marginRight: '10px' }} />
              <span style={{ color: '#a30909' }}>Olympiacos </span><span style={{ color: '#fff' }}>project</span>
            </a>
            <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarContent" aria-controls="navbarContent" aria-expanded="false" aria-label="Toggle navigation">
              <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarContent">
              <ul className="navbar-nav ms-auto">
                <li className="nav-item">
                  <a className="nav-link" href="index.html" style={{ color: 'white' }}>Home</a>
                </li>
                <li className="nav-item dropdown me-3" style={{ display: currentUser ? 'none' : 'block' }}>
                  <a className="nav-link" href="#" id="show-login-modal" data-bs-toggle="modal" data-bs-target="#login-modal">Login</a>
                </li>
                <li className="nav-item dropdown me-3" style={{ display: currentUser ? 'inline-block' : 'none' }}>
                  <a className="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                    <span id="username">{` ${currentUser}`}</span>
                  </a>
                  <ul className="dropdown-menu" aria-labelledby="username-dropdown">
                    <li><a className="dropdown-item" href="#" onClick={logout}>Log Out</a></li>
                  </ul>
                </li>
              </ul>
            </div>
          </div>
        </nav>

        <div className="container mt-5">
          <table className="table table-striped">
            <thead>
              <tr>
                <th>Actions</th>
                <th>Team Name</th>
                <th>Logo</th>
              </tr>
            </thead>
            <tbody>
              {teams.map(team => (
                <tr key={team.team_id}>
                  <td>
                    <button className="btn btn-danger" onClick={() => deleteTeam(team.team_id)}>Delete</button>
                  </td>
                  <td>{team.team_name}</td>
                  <td><img src={team.logo_url} className="team-logo" alt={`${team.team_name} logo`} /></td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </>
  );
};

export default TeamsTable;